@tailwind base;
@tailwind components;
@tailwind utilities;


#preview {
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 75vh;
  justify-content: center;
  background-size: cover;
  
}

h2 {
  text-transform: uppercase;
  /* text-shadow: 20; */
  letter-spacing: 4px;
  font-size: 30px;
  text-align: center;
  color: white;
}

.clock span {
  width: 100px;
  height: 80px;
  background: slateblue;
  opacity: 0.8;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.clock {
  display: flex;
}

.clock div {
  margin: 5px;
  position: relative;
}

.clock .text {
  height: 30px;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: darkblue;
}

.clock #ampm {
  bottom: 0;
  position: absolute;
  width: 60px;
  height: 30px;
  font-size: 25px;
  background: green;
}
